import Vue from 'vue'
import Router from 'vue-router'

import initPage from "@/components/InitPage.vue"
import playGame from "@/components/PlayGame.vue"
import loginPage from "@/components/LoginPage.vue"

Vue.use(Router)

export default new Router({
  mode: 'history', // history   url ,   (#) url 
  routes: [
      {
          path: "/initPage", // 
          name: "init", //    
          component: initPage //  
      },
      {
          path: "/loginPage", // 
          name: "login", //    
          component: loginPage //  
      },
      {
          path: "/playGame", // 
          name: "play", //    
          component: playGame //  
      },
  ]
})



